header {
    padding-top: 15px;
    padding-bottom: 25px;
    padding-left: 25px;
    height: 20px;
    width: 100%;
    font-size: 30px;
    font-family: "Roboto";
    font-weight: lighter;
    color: white;
    margin-bottom: 20px;
    background-image: linear-gradient(to right,rgb(2, 159, 250), blue);
}

.ingredients{
    font-weight: bold;
    }

.instructions{
    font-weight: bold;
}

.position {
    border: .5px solid rgb(2, 159, 250);
    border-radius: 5px;
    padding: 10px;
    width: 35%;
    position: absolute;
    left: 500px;
    top: 90px;
}

input {
    background: white;
    width: 20%;
    border: .5px solid rgb(2, 159, 250);
    border-radius: 5px;
    font-size: .8em;
    padding: 8px 12px;
    margin-right: 10px;
  }
  
  input:focus {
    outline:none;
    box-shadow: 4px 6px 26px 0px rgba(0, 0, 0, 0.317);
  }

button{
    background: white;
    border: .5px solid rgb(2, 159, 250);
    border-radius: 5px;
    width: 100px;
    padding: 8px 12px;
    font-size: .8em;
    cursor: pointer;
  }
  
  button:focus {
    outline:none;
    box-shadow: 4px 6px 26px 0px rgba(0, 0, 0, 0.317);
  }

  p {
    cursor: pointer;
  }
  